<template>
  <div class="container" :class="{dark}">
    <div class="head">
      我的倒数日<span v-show="list">{{ dues }}/{{ list && list.length }}</span>
    </div>
    <ul class="list">
      <li class="item" v-for="item in list" :key="item.key">
        <span class="label">{{ item.key }}</span>
        <div class="progress">
          <div
            :style="{
              width:
                item.remain > 0
                  ? `${((item.total - item.remain) * 100) / item.total}%`
                  : '100%',
            }"
          ></div>
        </div>
        <span class="remain"
          >{{ Math.abs(item.remain) }}天{{
            item.remain > 0 ? "后" : "前"
          }}</span
        >
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  props: ["_msg", "_created", "dark"],
  data() {
    return {
      msg: this._msg || "考研-2022/09/26,生日-2022/01/01,N2-2022/6/19",
      created: this._created || "2021/09/04",
      // list: [],
      dues: 0,
    };
  },
  computed: {
    list() {
      const _this = this;
      let _msg = this.msg.split(","),
        cur_date = new Date(),
        cur_time = cur_date.getTime(),
        created = new Date(this.created).getTime(),
        list = [];
      _msg.forEach((item) => {
        let _arr = item.split("-");
        let _date = new Date(_arr[1]),
          _time = _date.getTime();
        let ttime = _time - cur_time,
          days = Math.floor(ttime / (60 * 24 * 60 * 1000));
        let total_time = _time - created,
          t_days = Math.floor(total_time / (60 * 24 * 60 * 1000));
        if (days <= 0) {
          _this.dues += 1;
        }
        list.push({
          key: _arr[0],
          value: _arr[1],
          remain: days,
          total: t_days,
        });
      });
      console.log(list);
      return list;
    },
  },
  mounted() {},
  watch: {
    _msg(newVal) {
      if (newVal) {
        this.msg = newVal;
      }
    },
    _created(newVal) {
      if (newVal) {
        this.created = newVal;
      }
    },
  },
};
</script>
<style lang="less" scoped>
ul,
li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.dark {background-color:rgb(47 52 55);}
.container {
  width: 9.2rem;
  margin: 0.4rem auto;
  box-shadow: 0 0 0.266667rem 0.266667rem rgba(170, 151, 191, 0.2);
  border-radius: 0.266667rem;
  overflow: hidden;
  color: #ffffff;
  background-image: url(/assets/WechatIMG1235.jpeg);
  background-origin: center;
  padding: 0.666667rem;
  box-sizing: border-box;
  .head {
    font-weight: bold;
    margin-bottom: 0.266667rem;
    span {
      font-weight: normal;
      float: right;
    }
  }
  .list {
    .item {
      margin-top: 0.4rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .label {
        width: 1.6rem;
        text-align: right;
        display: inline-block;
      }
      .progress {
        width: 4.533333rem;
        height: 0.133333rem;
        background-color: rgba(255, 255, 255, 0.2);
        border-radius: 0.133333rem;
        overflow: hidden;
        div {
          background-color: rgba(255, 255, 255, 0.7);
          width: 0;
          height: 100%;
          border-radius: 0.133333rem;
        }
      }
      .remain {
        width: 1.333333rem;
        font-size: 0.32rem;
        em {
          font-style: normal;
          font-size: 0.266667rem;
        }
      }
    }
  }
}
</style>



