<template>
  <div class="container wrapper">
    <el-row :gutter="20" v-for="(row, index) in list" :key="'row-' + index">
      <el-col :span="8" v-for="item in row" :key="item.id">
        <el-card
          class="item-card"
          shadow="hover"
          style="margin-bottom: 10px"
          @click="handleSettingModal(item)"
        >
          <img :src="item.cover" alt="" class="image" />
          <span
            :id="`#/create/${item.id}/time`"
            class="leancloud_visitors clearfix"
            :data-flag-title="item && item.tag"
          >
            <i class="leancloud-visitors-count"></i>
          </span>
          <div class="bottom clearfix">
            <h3>{{ item.tag }}</h3>
            <span class="datetime-tag">{{ item.timestamp | relative }}</span>
            <el-button
              type="primary"
              icon="el-icon-edit"
              round
              class="edit-btn"
              :disabled="item | formatItemBtnDisable"
              @click="handleCreate(item.id)"
              >{{item | formatItemBtn}}</el-button
            >
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>
<script>

export default {
  data () {
    return {
      times: [],
      show_setting_modal: false,
      currentItem: null,
      currentComponent: 'highlight',
      list: {}
    }
  },
  mounted () {
    this.getDatas()
  },
  methods: {
    getDatas () {
      const _this = this
      this.$axios.get(`./resources.json?v=${Math.random()}`).then((res) => {
        let times = res.data.list.filter((item) => !item.hide)
        _this.times = times
        // eslint-disable-next-line one-var
        let list = {},
          len = times.length
        for (let i = 0; i < len; i++) {
          let _row = Math.floor(i / 3)
          if (list[_row]) {
            list[_row].push(times[i])
          } else {
            list[_row] = [times[i]]
          }
        }
        _this.list = list
      })
    },
    handleCreate (id) {
      splitbee.track('Select widget', {
        type: 'time',
        id
      })
      // window.location.href = `/#/create/${id}/time`;
      // return
      this.$router.replace(`/create/${id}/time`)
    },
    handleSettingModal (item) {
      if (item.link_type !== 'setting') return
      this.currentItem = item
      this.currentComponent = item.link
      this.show_setting_modal = true
    },
    handleModalClose () {
      this.currentItem = null
      this.currentComponent = null
      this.show_setting_modal = false
    }
  }
}
</script>
<style lang="less" scoped></style>



